.dropdown-menu {
  background: transparent;
  width: 200px;
  border-radius: 8px;
  position: absolute;
  top: 70px;
  list-style: none;
  text-align: start;
}

.dropdown-menu li {
  background: #00000057;
  cursor: pointer;
  border-radius: 15rem;
  margin-top: 1px;
  margin-bottom: 5px;
  transition: all 0.7s ease;
}

.dropdown-menu li:hover {
  background: rgba(223, 158, 97, 0.648);
  
}

.dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #fff;
  padding: 16px;
}

@media screen and (max-width: 960px) {
  .fa-caret-down {
    display: none;
  }
}
